import { useState, useEffect } from "react";
import "./hero_details.css";
import HeroComics from "./hero_comics/hero_comics";
import MarvelService from "../../services/MarvelService";
import Spinner from "../spinner/Spinner";
import ErrorMessage from "../error/Error";
import Skeleton from "../skeleton/skeleton";
import PropTypes from "prop-types";
const HeroDetails = (props) => {
  const [hero, setHero] = useState(null);
  const { loading, error, getCharacter, clearError } = MarvelService();

  useEffect(() => {
    console.log(props.HeroId);
    updateHero();
  }, [props.HeroId]);

  const updateHero = () => {
    clearError();
    const { HeroId } = props;
    if (!HeroId) {
      return;
    } else {
      getCharacter(HeroId).then(onHeroLoaded);
    }
  };
  const onHeroLoaded = (hero) => {
    setHero(hero);
  };

  const skeleton = hero || loading || error ? null : <Skeleton />;
  const ErrorMessage = error ? <ErrorMessage></ErrorMessage> : null;
  const spinner = loading ? <Spinner /> : null;
  const content = !(loading || error || !hero) ? <View hero={hero} /> : null;
  return (
    <div className="hero-details">
      {skeleton}
      {ErrorMessage}
      {spinner}
      {content}
    </div>
  );
};
const View = ({ hero }) => {
  const { name, description, thumbnail, homepage, wiki, comics } = hero;
  return (
    <>
      <div className="hero-details-head">
        <img src={thumbnail} alt={name} />
        <div className="hero-details-head-name">
          <h3>{name}</h3>
          <div className="hero-details-head-name-buttons">
            <button className="btn">
              <a href={homepage}>Homepage</a>
            </button>
            <button className="btn btn-active">
              <a href={wiki}>Wiki</a>
            </button>
          </div>
        </div>
      </div>
      <p className="hero-details-bio">
        {description.length > 0
          ? description.includes('<p class="Body">')
            ? description.replace('<p class="Body">', "")
            : description
          : "This hero is not so popular, right? Check out another one"}
      </p>
      <div className="hero-details-comics">
        <h4>Comics:</h4>
        <ul className="comics-list">
          {comics.length > 0 ? null : "There is no comics with this hero"}
          {comics.map((item, i) => {
            return (
              <li key={i} className="comics-item">
                {item.name}
              </li>
            );
          })}
        </ul>
      </div>
    </>
  );
};

export default HeroDetails;
