import { useState } from "react";
import HeroCard from "../hero_card/hero_card";
import HeroDetails from "../hero_details/hero_details";
import HeroList from "../hero_list/hero_list";
import ErrorBoundary from "../errorBoundary/ErrorBoundary";
import Search from "../search/search";

const MainPage = () => {
  const [selectedHero, setHero] = useState(null);
  const onHeroSelected = (id) => {
    setHero(id);
  };
  return (
    <>
      <img className="strange" src="img/bg_asset.png" alt="" />
      <ErrorBoundary>
        <HeroCard />
      </ErrorBoundary>
      <div className="content">
        <HeroList onHeroSelected={onHeroSelected} selectedId={selectedHero} />
        <div className="details__search">
          <ErrorBoundary>
            <HeroDetails HeroId={selectedHero} />
          </ErrorBoundary>
          <Search />
        </div>
      </div>
    </>
  );
};
export default MainPage;
