import { useState, useEffect } from "react";
import Spinner from "../spinner/Spinner";
import ErrorMessage from "../error/Error";
import MarvelService from "../../services/MarvelService";
import "./hero_list.css";

const CharList = (props) => {
  const [charList, setCharList] = useState([]);
  const [newItemLoading, setNewItemLoading] = useState(false);
  const [offset, setOffset] = useState(200);
  const [charEnded, setCharEnded] = useState(false);

  const { loading, error, getAllCharacters } = MarvelService();
  useEffect(() => {
    onRequest(offset, true);
  }, []);
  const onRequest = (offset, init) => {
    init ? setNewItemLoading(false) : setNewItemLoading(true);
    getAllCharacters(offset).then(onCharListLoaded);
  };
  const onCharListLoaded = (newCharList) => {
    let ended = false;
    if (newCharList.length < 9) {
      ended = true;
    }
    setCharList((charList) => [...charList, ...newCharList]);
    setNewItemLoading(false);
    setOffset((offset) => offset + 9);
    setCharEnded(ended);
  };
  console.log("hero");
  const renderItems = (arr) => {
    const items = arr.map((item) => {
      return (
        <li
          onKeyDown={() => props.onHeroSelected(item.id)}
          tabIndex={0}
          className="hero-item"
          key={item.id}
          onClick={() => props.onHeroSelected(item.id)}
          style={
            props.selectedId === item.id
              ? { boxShadow: "1px 1px 1px red" }
              : null
          }
        >
          <img
            src={item.thumbnail.path + "." + item.thumbnail.extension}
            alt={item.name}
          />
          <div className="hero-item-name">{item.name}</div>
        </li>
      );
    });
    return <ul className="hero-list">{items}</ul>;
  };

  const items = renderItems(charList);

  const errorMessage = error ? <ErrorMessage /> : null;
  const spinner = loading && !newItemLoading ? <Spinner /> : null;
  return (
    <div className="hero-list-wrapper">
      {errorMessage}
      {spinner}
      {items}
      <button
        style={{ display: charEnded ? "none" : "block" }}
        disabled={newItemLoading}
        onClick={() => onRequest(offset)}
        className="btn"
      >
        Load More
      </button>
    </div>
  );
};

export default CharList;
