import "./hero_card.css";
import { useState, useEffect } from "react";
import MarvelService from "../../services/MarvelService";
import Spinner from "../spinner/Spinner";
import ErrorMessage from "../error/Error";
const HeroCard = () => {
  const [hero, setHero] = useState({});

  const { loading, error, getCharacter, clearError } = MarvelService();
  useEffect(() => {
    updateHero();
    // return clearInterval(timerId);
  }, []);

  const onHeroLoaded = (hero) => {
    setHero(hero);
  };
  const updateHero = () => {
    clearError();
    const id = Math.floor(Math.random() * (1011400 - 1011000) + 1011000); //1017100
    getCharacter(id).then(onHeroLoaded);
  };
  const updateButton = () => {
    updateHero();
  };
  const { name, description, thumbnail, homepage, wiki } = hero;
  return (
    <div className="intro">
      <div className="intro-item intro-hero">
        {/* {error ? <ErrorMessage/> : loading ? <Spinner/> : <img src={thumbnail} alt="Random Hero" /> } */}
        {loading ? (
          <Spinner />
        ) : error ? (
          <ErrorMessage />
        ) : (
          <img src={thumbnail} alt="Random Hero" />
        )}
        <div className="intro-hero-desk">
          <h3>{name}</h3>
          <p>
            {description
              ? description
              : "This hero is not popular, right? Check his details on Wiki"}
          </p>
          <div className="intro-hero-desk-buttons">
            <a href={homepage} className="btn ">
              Homepage
            </a>
            <a href={wiki} className="btn btn-active">
              Wiki
            </a>
          </div>
        </div>
      </div>
      <div className="intro-item intro-random">
        <div className="intro-random-desk">
          <h3>
            Random character for today!
            <span>Do you want to get to know him better?</span>{" "}
          </h3>
          <p>Or choose another one</p>
          <button onClick={updateButton} className="btn">
            Try it
          </button>
        </div>
        <img src="img/Decoration.png" alt="" />
      </div>
    </div>
  );
};

const View = ({ hero }) => {
  return <div></div>;
};

export default HeroCard;
